@charset "UTF-8";
@import '../../slick/slick.css';
@import '../../slick/slick-theme.css';
html {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}

body {
  margin: auto;
}

a {
  text-decoration: none;
}

.slider .slide {
  transition: transform 0.3s ease-in-out;
}

.slider .slide img {
  width: 100%;
  max-width: 15em;
  height: auto;
  aspect-ratio: 1;
  object-fit: cover;
  object-position: 100% 0%;
  margin: 0 auto;
}

.slider .slick-center {
  /* height: 250px; */
  z-index: 9999;
  transform: scale(1.15) translateY(-5px);
}

.slick-list {
  padding: 45px 60px !important;
  margin-left: 30px !important;
}

/* THIS IS HOW TO ADD 'GLASS' EFFECT TO IMAGES */
.glass-effect {
  position: relative;
}

.glass-effect::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgb(19, 94, 172);
  mix-blend-mode: color;
  z-index: 1;
}

.glass-effect::before {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgba(19, 94, 172, 0.9);
  mix-blend-mode: multiply;
  z-index: 1;
}

.glass-effect > * {
  z-index: 2;
}

.navbox,
.nav_box {
  transition: all 0.3s ease-in-out !important;
}

